const _start = 'sore';
const _values = ['tore', 'tare'];
const _end = 'pare';

function in_path(word){
  return _values.reduce(
      (matched, next) => (next === word) || matched,
      null
  )

}

function arrToStr(arr){
  return arr.reduce((prev, curr) => prev + curr, '');
}

function arrFilled(arr){
  return arr.reduce((prev, curr) => (curr !== '') && prev, true)
}

function propsFromStatus(status){
  if (status === 'anchor'){
    return { readOnly: true }
  } else if (status === 'guessed') {
    return { readOnly: true }
  } else {
    return { }
  }
}

function Tile ({ value, status, onChange, focusRef }){
  return (
    <input
      className={"tile tile-" + status}
      onChange={onChange}
      value={value}
      ref={focusRef}
      {...propsFromStatus(status)}
     />
  )
}

function Row({ numLetters, handleCorrect, word, status }){
  const [tiles, setTiles] = useState(
    Array(numLetters).fill('').map(
      (_, index) => (word == null ? '' : word[index])
    )
  );
  const tileRefs = [useRef(null), useRef(null), useRef(null), useRef(null)];


  function incorrectGuess(){
    //local stuff
    console.log('incorrect');
  }

  function correctGuess(word){
    // local stuff
    handleCorrect(word);
  }

  function onChange(event, i){
    const nextTiles = tiles.slice();
    nextTiles[i] = event.target.value;
    console.log(nextTiles);
    if (!arrFilled(nextTiles)) {
      tileRefs[i+1].current.focus();
    } else {
      const word = arrToStr(nextTiles)
      if (in_path(word) === true)  {
        correctGuess(word);
      } else {
        nextTiles.fill('');
        tileRefs[0].current.focus();
        incorrectGuess();
      }

    }
    setTiles(nextTiles);
  }


  return (
    <div className="row">
      {
        tiles.map(
          (_, i) => (
            <Tile 
              value={tiles[i]} 
              status={status}
              onChange={(event) => onChange(event, i)} 
              focusRef={tileRefs[i]}
            />
          )
         )
      }
    </div>
  )
}

export default function Board() {


  const [squares, setSquares] = useState(intial());
  const numLetters = 4;

  function intial(){
    const tempSqaure = new Array(10).fill(null).map(() => ({visible: false, word: null, status: null}));
    tempSqaure[0].word = _start;
    tempSqaure[0].visible = true;
    tempSqaure[0].status = 'anchor';
    tempSqaure[9].word = _end;
    tempSqaure[9].visible = true;
    tempSqaure[9].status = 'anchor';
    tempSqaure[1].visible = true;
    tempSqaure[1].visible = 'active';
    return tempSqaure;
  }

  function handleCorrect(word){
    const nextSquares = squares.slice();
    const pathLength = nextSquares.reduce((count, curr) => curr.word !== null ? count + 1: count, 0);
    nextSquares[pathLength - 1].word = word;
    nextSquares[pathLength - 1].status = 'guessed';
    nextSquares[pathLength].visible = true;
    nextSquares[pathLength].status = 'active';
    setSquares(nextSquares);
  }

  intial();

  return (
    <div className="board">
      {
        squares.map(
          (square, i) =>
          square.visible ?
          <Row
            numLetters={numLetters}
            handleCorrect={handleCorrect}
            word={square.word}
            rowIndex={i}
            status={square.status}
          />
          :
          null
        )
      }
    </div>
  )
}



